<template>
  <div class="collect">
    <PersonalHeader></PersonalHeader>
    <div class="wrapper clearfix content">
      <PersonalMenu class="fl" currentMenu="3"></PersonalMenu>
      <div class="fr right">
        <div class="header clearfix" @click="tabChange">
          <div class="tab fl" data-type="1" :class="[type == 1? 'currentTab':'']">项目收藏</div>
          <div class="tab fl" data-type="2" :class="[type == 2? 'currentTab':'']">文章收藏</div>
        </div>
        <div class="per" v-if="type == '1'">
          <router-link :to="'/detail?id='+item.id" class="item" v-for="item in data" :key="'pro'+item.id">
            <img src="../../../static/image/evaluate.png" alt="">
            <div class="right">
              <div class="name clearfix">
                <span class="fl">{{item.item_name}}</span>
                <span class="money fr">￥{{item.money}}万</span>
              </div>
              <div class="detail">
                <div class="info">
                  所属行业：<span>{{item.cate_name}}</span>
                </div>
                <div class="info">
                  直营店：<span>{{item.shop_count}}家</span>
                </div>
                <div class="info">
                  申请人数：<span>{{item.apply}}</span>
                </div>

                <div class="info">
                  加盟店：<span>{{item.fran_store_num}}家</span>
                </div>
              </div>
            </div>
            <div class="btns">
              <el-button type="primary" @click.prevent="dialogFormVisible = true,productid = item.id">立即咨询</el-button>
              <el-button @click.prevent="cancelCollect" data-type="2" :data-id="item.id">取消收藏</el-button>
            </div>
          </router-link>
        </div>
          <div v-else>
              <div class="tabs clearfix" @click="articleTypeChange">
                <div class="tab fl" data-type="1" :class="[articleType == 1? 'currentType':'']">加盟咨询</div>
                <div class="tab fl" data-type="2" :class="[articleType == 2? 'currentType':'']">加盟攻略</div>
                  <div class="tab fl" data-type="0" :class="[articleType == 0? 'currentType':'']">行业报告</div>
              </div>
            <div class="article" >
              <dl class="listDL clearfix" v-for="item in data" :key="'art'+item.id">
                <router-link :to="'consultDetail?id='+item.id">
                  <dt>
                    <img :src="item.img">
                  </dt>
                  <dd>
                    <h3>{{item.title}}</h3>
                    <p class="desc">{{item.sign}}</p>
                    <p class="icon">
                      <span><i class="iconfont icon-data"></i>{{item.addtime}}</span>
                      <span><i class="iconfont icon-look"></i>{{item.read_num}}</span>
                    </p>
                  </dd>
                  <div class="btns">
                    <el-button @click.prevent="cancelCollect" data-type="3" :data-id="item.id">取消收藏</el-button>
                  </div>
                </router-link>
              </dl>
            </div>
          </div>
        <el-pagination
          background
          layout="prev, pager, next"
          :total="count"
          class="pages"
          @current-change="pageChange"
        >
        </el-pagination>
      </div>
    </div>

    <!-- 咨询弹窗  -->
    <el-dialog title="立即咨询" :visible.sync="dialogFormVisible" center width="30%">
      <el-form :model="form" :rules="rules" ref="form" >
        <el-form-item label="姓名" :label-width="formLabelWidth" prop="name">
          <el-input v-model="form.name" autocomplete="off" placeholder="请输入您的姓名"></el-input>
        </el-form-item>
        <el-form-item label="性别" :label-width="formLabelWidth" prop="sex">
          <el-radio-group v-model="form.sex">
            <el-radio  label="1">男</el-radio>
            <el-radio  label="0">女</el-radio>
          </el-radio-group>

        </el-form-item>
        <el-form-item label="手机" :label-width="formLabelWidth" prop="phone">
          <el-input v-model="form.phone" autocomplete="off" placeholder="请输入您的手机号"></el-input>
        </el-form-item>
        <el-form-item label="图片验证" :label-width="formLabelWidth" class="code" prop="img_code">
          <el-input placeholder="请输入图片验证码" v-model="form.img_code" class="codeLeft"></el-input>
          <img :src="imgCode" class="codeRight" alt="" @click="getImgCode">
        </el-form-item>
        <el-form-item label="验证码" :label-width="formLabelWidth" class="code" prop="code">
          <el-input placeholder="请输入手机验证码" v-model="form.code"></el-input>
          <div class="codeRight">
            <div class="codeBtn" @click="getCode" v-if="showCode">
              获取验证码
            </div>
            <div class="codeBtn outTime" v-else>
              {{outTime}}S后重新获取
            </div>
          </div>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth"  prop="content">
          <el-input
            type="textarea"
            :rows="2"
            placeholder="请输入您要咨询的内容"
            v-model="form.content"
          >
          </el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <!--          <el-button type="primary"  native-type="submit" @click="dialogFormVisible = false">提 交</el-button>-->
        <el-button type="primary" @click="submitForm('form')">提 交</el-button>
      </div>

    </el-dialog>
    <!-- 咨询弹窗  -->

  </div>
</template>

<script>
  import PersonalHeader from '@/components/PersonalHeader'
  import PersonalMenu from '@/components/PersonalMenu'
  import api from '@/api/personal-api'
  import commonApi from '@/api/common-api'

  export default {
    name: "collect",
    data() {
      return {
        form: {},
        dialogFormVisible: false,
        formLabelWidth: '80px',
        type: '1', //type 1 项目收藏 2 文章收藏
        page: 0,
        count: 0,
        data: '',
        articleType: '1',
        showCode: true,
        outTime: 60,
        imgCode: '',
        productid:'',
        rules: {
          name: [
            { required: true, message: '请输入活动名称', trigger: 'blur' },
          ],

          sex: [
            { required: true, message: '请选择您的性别', trigger: 'change' }
          ],
          phone: [
            { required: true, message: '请填写您的手机号', trigger: 'blur' },
            {len: 11,message: '手机号码格式不正确',trigger:'blur'}
          ],
          img_code: [
            { required: true, message: '请填写图片验证码', trigger: 'blur' },
          ],
          code: [
            { required: true, message: '请输入手机验证码', trigger: 'blur' },
          ],
          content: [
            { required: true, message: '请输入您要咨询的内容', trigger: 'blur' },
          ],
        }
      }

    },
    created(){
      this.getProductList();
      this.getImgCode();
    },
    methods:{

      /**
       * 立即咨询 提交表单
       */
      submitForm:function (formName){
        console.log(this.form);
        const {productid} = this;
        this.dialogFormVisible = false;
        this.$refs[formName].validate((valid) => {
          if (valid) {
            api.submitConsult({type: 0,item_id: productid,...this.form}).then(data => {
              this.$message({message:'提交成功',type:'success'});
              this.resetForm(formName);
            })
          } else {
            console.log('error submit!!');
            return false;
          }
        });
      },
      /**
       * 重置表单
       */
      resetForm(formName) {
        this.$refs[formName].resetFields();
      },
      /**
       * 获取项目列表
       */
      getProductList(){
        const {page} = this;
        api.getProductList({type:1,page}).then(data => {
              this.data = data.data;
              this.count = data.count;
          });
      },

      /**
       * 文章类型改变事件
       */
      articleTypeChange: function({target: {dataset:{type}}}){
          this.articleType = type;
          this.getArticleList();
      },

      /**
       * 获取文章列表
       */
      getArticleList(){
        const {page,articleType:type} = this;
        api.getArticleList({type,page,is_fow:1}).then(data => {
          this.data = data.data;
          this.count = data.count;
        });
      },

      /**
       * 获取图片验证码
       */
      getImgCode: function(){
        commonApi.getImgCode().then(data => {
            this.imgCode = data;
          })
      },

      /**
       * 项目和文章相互切换
       */
      tabChange({target: {dataset: {type}}}){
          this.data = [];
          this.type = type;
          this.page = 0;
          if(type == '1'){
            this.getProductList();
          }else{
            this.getArticleList();
          }
      },

      /**
       * 取消收藏
       */
      cancelCollect: async function({currentTarget: {dataset:{id,type}}}){
        const data =  await api.cancelCollect({id,type});
        this.$message.success("取消收藏成功")
        if (type == 2){
          this.getProductList();
        }else{
          this.getArticleList();
        }
      },

      /**
       * 点击分页
       * @param e
       */
      pageChange:function(e){
        const {type } =this.data;
        this.page = e-1;
        window.scrollTo(0,0);
        if(type == '1'){
          this.getProductList();
        }else{
          this.getArticleList();
        }
      },

      /**
       * 获取验证码
       */
      getCode: function() {
        console.log("this.form;",this.form);
        const {phone = '',img_code=''} = this.form;
        const _this = this;
        if (!this.validatePhone(phone)) {
          return
        }
        this.showCode = false;
        let intervalID = setInterval(function() {
          if (_this.outTime == 0) {
            _this.showCode = true;
            _this.outTime = 60;
            clearInterval(intervalID)
          }
          _this.outTime--;
        }, 1000)
        commonApi.getPhoneCode({phone,img_code}).then(data => {
          this.$message({message:'验证码获取成功',type: 'success'});
        });
      },

      /**
       * 验证手机格式
       * @param {Object} phone
       */
      validatePhone: function(phone) {
        if (!phone) {
          this.$message.error('请输入手机号码');
          return false;
        }
        if (phone.length != 11) {
          this.$message.error('手机号码格式不正确');
          return false;
        }
        return true;
      },

    },
    components: {
      PersonalHeader,
      PersonalMenu
    }
  }
</script>

<style scoped type="text/less" lang="less">
  .collect {
    background-color: #F4F5FA;
    padding-bottom: 120px;

    .content {
      padding-top: 20px;

      .right {
        width: 950px;
        background-color: #fff;

        .header {
          height: 60px;
          line-height: 58px;
          color: #999;
          font-size: 16px;
          padding: 0 30px;
          cursor: pointer;
          .tab {
            margin-right: 45px;
          }

          .currentTab {
            color: #333;
            font-weight: bold;
            border-bottom: 2px solid #579EFA;
          }

        }
        .tabs {
          height: 60px;
          line-height: 58px;
          color: #999;
          font-size: 16px;
          padding: 0 30px;
          cursor: pointer;
          background-color: #F0F7FF;
          .tab {
            margin-right: 45px;
          }

          .currentType {
            color: #5389F7;
          }

        }

      }
    }
  }
  .per{
    padding: 0 30px;
    .item{
      position: relative;
      display: flex;
      font-size: 14px;
      color: #999;
      padding: 20px 0;
      border-bottom: 1px solid #E3E4E5;
      .right{
        margin-left: 22px;
        .name{
          font-size: 18px;
          color: #333;
          font-weight: bold;
          margin-top: 24px;
          .money{
            color: #FF7E00;
            font-size: 16px;

          }
        }
      }
      img{
        width: 140px;
        height: 140px;
      }
      .detail{
        width: 320px;
        margin-top: 15px;

        .info{
          display: inline-block;
          margin-bottom: 10px;
          span{
            display: inline-block;
            width: 80px;
          }
        }
      }
    }

    .item:last-child{
      border: 0;
    }
  }

  .article{
    padding: 0 30px;
    .listDL{
      position: relative;
      padding: 20px 0;
      border-bottom: 1px solid #E3E4E5;
      dt{
        float: left;
        width: 198px;
        height: 140px;
        img{
          display: block;
          width: 198px;
          height: 140px;
        }
      }
      dd{
        float: left;
        width: 580px;
        margin-left: 25px;
        h3{
          font-size: 18px;
          color: #000;
          margin-top: 12px;
        }
        .desc{
          line-height: 26px;
          overflow: hidden;
          text-overflow: ellipsis;
          display: -webkit-box;
          -webkit-line-clamp: 2;
          -webkit-box-orient: vertical;
          margin-top: 13px;
        }
        .icon{
          color: #999;
          line-height: 20px;
          margin-top: 12px;
          span{
            margin-right: 35px;
            i{
              color: #ccc;
              margin-right: 10px;
            }
          }
        }
      }

      .reply{
        width: 850px;
        font-size: 14px;
        color: #808080;
        padding: 10px 20px;
        background-color: #F4F5FA;
        margin-top: 20px;
      }
    }
    .listDL:last-child{
      border: 0;
    }
  }

  .btns{
    position: absolute;
    bottom: 10px;
    right: 0px;
    display: flex;
    text-align: right;
  }

  .dialog-footer {
    text-align: center;
  }
  .pages{
    text-align: center;
    margin: 34px 0;
  }


  /* 咨询弹窗 */
  .code .el-input{
    width: 186px;
  }
  .codeRight{
    height: 44px;
    width: 105px;
    vertical-align: middle;
    display: inline-block;
    margin-top: -4px;
    line-height: 44px;
    cursor: pointer;
  }
  .codeBtn {
    height: 41px;
    overflow: hidden;
    font-size: 14px;
    color: #4272FF;
    text-align: center;
    border: 1px solid #DCDFE6;
    border-radius: 10px;
  }

  .outTime {
    color: #666;
  }
  /* 咨询弹窗 */
</style>



